@use 'variables' as v;
@use 'base/mixins' as m;


html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
    scroll-behavior:smooth;
}
div[id]{
    scroll-margin-top: 7rem;
    padding-top: 12rem;

    @include m.tablet{
        padding-top: 4rem;
    }
}
#titulo{
    padding-top: 18rem;
    @include m.tablet{
        padding-top: 8rem;
    }
}
#login{
    padding-top: 18rem;
    @include m.tablet{
        padding-top: 10rem;
    }
}

body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$blanco;
    color: v.$negro;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
