@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-image: url(../img/img2.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-color: rgba(0, 0, 0, 0.212);
    padding: 2rem 0;
}
.cotenido-footer{
    display: flex;
    align-items: center;
    flex-direction: column;

    
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }

    .ubicacion{
        border-bottom: 1px solid v.$negro;
        margin: 1rem 0;
        text-align: center;
        width: 95%;

        h3{
            font-size: 1.8rem;
            
        }
        span{
            font-size: 1.8rem;
            font-weight: v.$bold;
            margin: 0;
            margin-bottom: 1rem;
            // vertical-align: middle;
            margin-bottom: 1rem;
            display: block;
        }
        
        a{
           color: v.$negro;

           &:hover{
               border-bottom: 1px solid v.$azul;
               color: v.$azul;
           }

        }

        .ima{
            display:inline-block;
            vertical-align: top;
            margin-bottom: 6.5rem;
        }

        img{
            max-height: 2rem;
            display:inline-block;
            // vertical-align: top;            
        }
       
    }
}