@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-bottom: 3rem;
    p{
        font-size: 1.4rem;
        color: v.$gris;
        margin: 2rem 0 0 0;
    }

    fieldset{
        border-color: v.$azul;
    }
    
    legend{
        font-size: 2rem;
        color: v.$negro;
    }
    
    label{
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
    }
     
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$azul;
        border-radius: 1rem;
        margin-bottom: 2rem;

    }
    textarea{
        height: 20rem;
    }

}
.boton-azul{
    @include m.boton(v.$azul, v.$blanco);
    margin: 5rem auto;
    border-radius: 1rem;
}
.alerta{
    padding: .5rem;
    text-align: center;
    color: v.$blanco;
    font-weight: v.$bold;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error{
        background-color: v.$rojo;
    }

    &.exito{
        background-color: v.$verde;
    }
}