@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion{
    align-items: center;
    display: none;


    @include m.tablet{
        display: flex;
        
    }
    a{  
        color: v.$negro;
        text-decoration: none;
        font-size: 2rem;
        display: block;
        text-transform: uppercase;
        font-weight: 530;
        text-align: center;
        margin-top: 1rem;

        &:hover{
            color: v.$azul;
        }

        @include m.tablet{
            margin-left: 2rem;
            margin-top: 0;
        }
    }
}

.logo{
        width: 30rem;
}

// Menu de hambuerguesa

.mobile-menu{

    @include m.tablet{
        display: none;
    }

    img{
        width: 5rem;
        margin-top: 2rem;
    }
}

.mostrar{
    display: flex;
    flex-direction: column;

    @include m.tablet{
        flex-direction: row;
    }
}