html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

div[id] {
  scroll-margin-top: 7rem;
  padding-top: 12rem;
}
@media (min-width: 768px) {
  div[id] {
    padding-top: 4rem;
  }
}

#titulo {
  padding-top: 18rem;
}
@media (min-width: 768px) {
  #titulo {
    padding-top: 8rem;
  }
}

#login {
  padding-top: 18rem;
}
@media (min-width: 768px) {
  #login {
    padding-top: 10rem;
  }
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  font-size: 2rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

.header {
  background-image: url(../img/img2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: white;
  padding: 1rem 0 1rem 0;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
}

.footer {
  background-image: url(../img/img2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: rgba(0, 0, 0, 0.212);
  padding: 2rem 0;
}

.cotenido-footer {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .cotenido-footer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
.cotenido-footer .ubicacion {
  border-bottom: 1px solid #1a1b15;
  margin: 1rem 0;
  text-align: center;
  width: 95%;
}
.cotenido-footer .ubicacion h3 {
  font-size: 1.8rem;
}
.cotenido-footer .ubicacion span {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0;
  margin-bottom: 1rem;
  margin-bottom: 1rem;
  display: block;
}
.cotenido-footer .ubicacion a {
  color: #1a1b15;
}
.cotenido-footer .ubicacion a:hover {
  border-bottom: 1px solid #0da6f3;
  color: #0da6f3;
}
.cotenido-footer .ubicacion .ima {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 6.5rem;
}
.cotenido-footer .ubicacion img {
  max-height: 2rem;
  display: inline-block;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  align-items: center;
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
  }
}
.navegacion a {
  color: #1a1b15;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-transform: uppercase;
  font-weight: 530;
  text-align: center;
  margin-top: 1rem;
}
.navegacion a:hover {
  color: #0da6f3;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-left: 2rem;
    margin-top: 0;
  }
}

.logo {
  width: 30rem;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  margin-top: 2rem;
}

.mostrar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .mostrar {
    flex-direction: row;
  }
}

.inicio {
  background-image: url(../img/img1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 40rem;
}
@media (min-width: 768px) {
  .inicio {
    min-height: 90rem;
  }
}

.titulo {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  min-height: 40rem;
}
@media (min-width: 768px) {
  .titulo {
    min-height: 90rem;
  }
}
.titulo h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 50rem;
  background: rgba(0, 0, 0, 0.424);
  padding: 1rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .titulo h1 {
    font-size: 4rem;
  }
}

.informacion {
  margin-top: 1rem;
  background-image: url(../img/img2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 40rem;
}
.informacion h4 {
  margin: 0 1rem;
}
@media (min-width: 768px) {
  .informacion {
    min-height: 100rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
  }
  .grid .datos:nth-child(3) {
    grid-column: 1/4;
  }
}

.datos {
  background-color: #ff8d2f;
  margin-bottom: 1rem;
}
.datos p {
  font-size: 2rem;
  font-weight: 400;
  padding: 1rem;
  margin: 0;
}
.datos h3 {
  padding: 2rem;
  margin: 0;
}

.telescopio {
  background-image: url(/build/img/img8.png);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-color: #ee91ff;
}

.cuaderno {
  background-image: url(/build/img/img6.png);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-color: #91ffa0;
}

.requisitos {
  list-style: none;
  padding: 0 1rem 1rem 1rem;
}
.requisitos li {
  padding-bottom: 2rem;
  font-size: 2rem;
}

.coete {
  background-image: url(/build/img/img4.png);
  background-repeat: no-repeat;
  background-position: left center;
  background-color: #ff9191;
}
@media (min-width: 768px) {
  .coete {
    background-image: url(/build/img/img3.png);
    background-repeat: no-repeat;
    background-position: right center;
    font-weight: 400;
  }
}

.premio {
  background-image: url(/build/img/img21.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 40rem;
}

.nosotros {
  background-image: url(/build/img/img2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: rgba(0, 136, 255, 0.335);
  min-height: 100rem;
}
@media (min-width: 768px) {
  .nosotros .contedendor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
  }
}

.mision {
  margin-top: 2rem;
}
.mision p {
  line-height: 2.5rem;
  text-align: justify;
  margin: 0 2rem;
}

.img-mision {
  margin-top: 2rem;
  background-image: url(/build/img/img17.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  min-height: 40rem;
  border-radius: 1rem;
}

.nos {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.vision {
  margin-top: 2rem;
}
.vision p {
  line-height: 2.5rem;
  text-align: justify;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .vision h2 {
    margin-top: 4rem;
  }
  .vision p {
    margin-top: 10rem;
  }
}

.img-vision {
  order: 1;
  margin-top: 2rem;
  background-image: url(/build/img/img18.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  min-height: 40rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .img-vision {
    order: 0;
    min-height: 50rem;
  }
}

.objetivo {
  order: 2;
  margin-top: 2rem;
}
.objetivo p {
  line-height: 2.5rem;
  text-align: justify;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .objetivo {
    order: 0;
  }
  .objetivo h2 {
    margin-top: 4rem;
  }
  .objetivo p {
    margin-top: 10rem;
  }
}

.img-objetivo {
  order: 3;
  margin-top: 2rem;
  background-image: url(/build/img/img10.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  min-height: 40rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .img-objetivo {
    order: 0;
    min-height: 50rem;
  }
}

.somos {
  order: 4;
  margin: 2rem 0;
}
.somos p {
  line-height: 2.5rem;
  text-align: justify;
  margin: 0 2rem;
}

.img-somos {
  order: 5;
  margin: 2rem 0;
  background-image: url(/build/img/img20.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  min-height: 40rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .img-somos {
    order: 0;
    min-height: 50rem;
  }
}

.contacto {
  margin-top: 1rem;
  background-image: url(../img/img2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 40rem;
}

.red {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.red .redes img {
  margin: 1rem;
  max-width: 4rem;
}
@media (min-width: 768px) {
  .red .redes img {
    margin: 2rem;
    max-width: 7.5rem;
  }
}

.formulario {
  margin-bottom: 3rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #e1e1e1;
  margin: 2rem 0 0 0;
}
.formulario fieldset {
  border-color: #0da6f3;
}
.formulario legend {
  font-size: 2rem;
  color: #1a1b15;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #0da6f3;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}

.boton-azul {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 5rem auto;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton-azul {
    width: auto;
  }
}
.boton-azul:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}