@use 'base/variables' as v;
@use 'base/mixins' as m;

.inicio{
    background-image: url(../img/img1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 40rem;
    @include m.tablet{
        min-height: 90rem;
    }
}

.titulo{
    display: flex;
    flex-direction: column;
    align-items:flex-end;
    // justify-content: center;
    min-height: 40rem;
    
    @include m.tablet{
        min-height: 90rem;
    }

    h1{
        text-align: left;
        color: v.$blanco;
        font-weight: v.$bold;
        line-height: 2;
        max-width: 50rem;
        background: rgba(0, 0, 0, 0.424);
        padding: 1rem;
        font-size: 2rem;

        @include m.tablet{
            font-size: 4rem;
        }

    }
}  // Fin de Titulo imagen niño

.informacion{
    margin-top: 1rem;
    background-image: url(../img/img2.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 40rem;

    h4{
        margin: 0 1rem;
    }

    @include m.tablet{
        min-height: 100rem;
        grid-template-columns:  repeat(2, 1fr);
    }

}
.grid{
    @include m.tablet{
        display: grid;
        grid-template-columns:  repeat(2, 1fr);
        grid-gap: 1rem;

        .datos:nth-child(3){
            grid-column: 1 / 4 ;
        }
    }
}

.datos{
    background-color: v.$naranja;
    margin-bottom: 1rem;

    p{
        font-size: 2rem;
        font-weight: v.$regular;
        padding:  1rem;
        margin: 0;
    }

    h3{
        padding: 2rem;
        margin: 0;
    }

}

.telescopio{
    background-image:url(/build/img/img8.png);
    background-repeat: no-repeat;
    background-position: right bottom;
    background-color: rgb(238, 145, 255);

}
.cuaderno{
    background-image:url(/build/img/img6.png);
    background-repeat: no-repeat;
    background-position: right bottom;
    background-color: rgb(145, 255, 160);


}

.requisitos{
    list-style: none;
    padding:  0 1rem 1rem 1rem;
    li{
        padding-bottom: 2rem;
        font-size: 2rem;
    }
}
.coete{
    background-image:url(/build/img/img4.png);
    background-repeat: no-repeat;
    background-position: left center;
    background-color: rgb(255, 145, 145);


    @include m.tablet{
        background-image:url(/build/img/img3.png);
        background-repeat: no-repeat;
        background-position: right center;
        font-weight: v.$regular;
    }
} 
.premio{
    background-image:url(/build/img/img21.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 40rem;

}
// Fin de requisitos

.nosotros{
    background-image: url(/build/img/img2.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-color: rgba(0, 136, 255, 0.335);
    min-height: 100rem;

    .contedendor{
        
        @include m.tablet{
            display: grid;
            grid-template-columns:  repeat(2, 1fr);
            grid-gap: 1rem;
        }

    }
} //nosotros

.mision{
    margin-top: 2rem;
    
    p{
        line-height: 2.5rem;
        text-align: justify;
        margin: 0 2rem;
    }
}
.img-mision{
        margin-top: 2rem;
        background-image:url(/build/img/img17.jpg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;

        min-height: 40rem;
        border-radius: 1rem;
} // Fin mision 
.nos{
    display: flex;
    flex-direction: column;
     @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2, 1fr );
     }
}
.vision{
        margin-top: 2rem;
    p{
        line-height: 2.5rem;
        text-align: justify;
        margin: 0 2rem;
    }

    @include m.tablet{
        h2{
            margin-top: 4rem;
        }

        p{
            margin-top: 10rem;
        }
    }
}
.img-vision{
    order: 1;
    margin-top: 2rem;
        background-image:url(/build/img/img18.jpg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        min-height: 40rem;
        border-radius: 1rem;

        @include m.tablet{
            order: 0;
            min-height: 50rem;

        }
} //fin de vision

.objetivo{
    order: 2;
    margin-top: 2rem;
    
    p{
        line-height: 2.5rem;
        text-align: justify;
        margin: 0 2rem;
    }

    @include m.tablet{
        order: 0;
        h2{
            margin-top: 4rem;
        }

        p{
            margin-top: 10rem;
        }
    }
}

.img-objetivo  {
    order: 3;
    margin-top: 2rem;
        background-image:url(/build/img/img10.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        min-height: 40rem;
        border-radius: 1rem;

        @include m.tablet{
            order: 0;
            min-height: 50rem;
        }
}  //fin objetivo

.somos{
    order: 4;
    margin: 2rem 0;
    
    p{
        line-height: 2.5rem;
        text-align: justify;
        margin: 0 2rem;
    }

}

.img-somos  {
        order: 5;
        margin: 2rem 0;
        background-image:url(/build/img/img20.jpg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        min-height: 40rem;
        border-radius: 1rem;

        @include m.tablet{
            order: 0;
            min-height: 50rem;
        }
}  //fin objetivo
.contacto{
    margin-top: 1rem;
    background-image: url(../img/img2.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 40rem; 
}
.red{    
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .redes{
        img{
            margin: 1rem;
            max-width: 4rem;
        }
        @include m.tablet{
            img{
                margin: 2rem;
                max-width: 7.5rem;
            }
        }

        
    }
}
